import { useCallback } from 'react';
import { usePathname } from 'next/navigation';
import { 
  formatDateWithTimeZone, 
  formatRelativeTime, 
  formatSmartDate
} from '@/utils/date';
import type { SupportedLanguage } from '@/types/mypage-common';

export function useLanguage() {
  const pathname = usePathname();

  // ÌòÑÏû¨ Ïñ∏Ïñ¥ Ï∂îÏ∂ú
  const getCurrentLanguage = useCallback((): SupportedLanguage => {
    const lang = pathname.split('/')[1];
    switch (lang) {
      case 'ko':
        return 'ko';
      case 'ja':
        return 'ja';
      case 'zh':
        return 'zh';
      case 'id':
        return 'id';
      default:
        return 'en';
    }
  }, [pathname]);

  // Í∏∞Î≥∏ ÎÇ†Ïßú Ìè¨Îß∑ÌåÖ (Í∏∞Ï°¥ Ìò∏ÌôòÏÑ± Ïú†ÏßÄ)
  const formatDate = useCallback((dateString: string) => {
    const currentLang = getCurrentLanguage();
    return formatDateWithTimeZone(dateString, undefined, currentLang);
  }, [getCurrentLanguage]);

  // ÏÉÅÎåÄÏ†Å ÏãúÍ∞Ñ Ìè¨Îß∑ÌåÖ (Ïòà: "3ÏãúÍ∞Ñ Ï†Ñ", "2Ïùº Ï†Ñ")
  const formatRelativeDate = useCallback((
    dateString: string,
    options?: {
      useAbsolute?: boolean;
      absoluteThreshold?: number;
      showTime?: boolean;
    }
  ) => {
    const currentLang = getCurrentLanguage();
    return formatRelativeTime(dateString, currentLang, options);
  }, [getCurrentLanguage]);

  // Ïä§ÎßàÌä∏ ÎÇ†Ïßú Ìè¨Îß∑ÌåÖ (Ïª®ÌÖçÏä§Ìä∏Ïóê Îî∞Îùº ÏûêÎèô ÏÑ†ÌÉù)
  const formatSmartDateInternal = useCallback((
    dateString: string,
    context: 'post' | 'comment' | 'detailed' = 'post'
  ) => {
    const currentLang = getCurrentLanguage();
    return formatSmartDate(dateString, currentLang, context);
  }, [getCurrentLanguage]);

  // Post/Comment Ï†ÑÏö© ÎÇ†Ïßú Ìè¨Îß∑ÌÑ∞
  const formatPostDate = useCallback((dateString: string) => {
    return formatSmartDateInternal(dateString, 'post');
  }, [formatSmartDateInternal]);

  const formatCommentDate = useCallback((dateString: string) => {
    return formatSmartDateInternal(dateString, 'comment');
  }, [formatSmartDateInternal]);



  // Îã§Íµ≠Ïñ¥ ÌÖçÏä§Ìä∏ Ï≤òÎ¶¨ (Í∏∞Ï°¥ Í∏∞Îä• Ïú†ÏßÄ)
  const getLocalizedText = useCallback((text: any): string => {
    try {
      if (text === null || text === undefined || text === '') {
        return '';
      }
      
      if (typeof text === 'string') {
        return text;
      }
      
      if (typeof text === 'number' || typeof text === 'boolean') {
        return String(text);
      }
      
      if (Array.isArray(text)) {
        return text.length > 0 ? String(text[0]) : '';
      }
      
      if (text.$$typeof || text._owner || text.type || text.props) {
        console.error('üö® React Ïª¥Ìè¨ÎÑåÌä∏ Í∞êÏßÄ! Í∞ïÏ†ú Î≥ÄÌôò:', text);
        return '[React Element Detected]';
      }
      
      if (typeof text === 'object' && text !== null) {
        const currentLang = getCurrentLanguage();
        
        let result: any = '';
        
        if (text[currentLang] !== undefined && text[currentLang] !== null) {
          result = text[currentLang];
        } else if (text.en !== undefined && text.en !== null) {
          result = text.en;
        } else if (text.ko !== undefined && text.ko !== null) {
          result = text.ko;
        } else {
          const keys = Object.keys(text);
          for (const key of keys) {
            if (text[key] !== null && text[key] !== undefined && text[key] !== '') {
              result = text[key];
              break;
            }
          }
        }
        
        if (typeof result === 'object' && result !== null) {
          return getLocalizedText(result);
        }
        
        return String(result || '');
      }
      
      return String(text);
      
    } catch (error) {
      console.error('üí• getLocalizedText ÏπòÎ™ÖÏ†Å Ïò§Î•ò:', {
        error: error instanceof Error ? error.message : String(error),
        input: text,
        inputType: typeof text
      });
      
      try {
        return JSON.stringify(text);
      } catch {
        return '[Ï≤òÎ¶¨ Î∂àÍ∞ÄÎä•Ìïú Îç∞Ïù¥ÌÑ∞]';
      }
    }
  }, [getCurrentLanguage]);

      return {
      // Í∏∞Î≥∏ Ï†ïÎ≥¥
      currentLanguage: getCurrentLanguage(),
      getCurrentLanguage,
      
      // ÎÇ†Ïßú Ìè¨Îß∑ÌåÖ Ìï®ÏàòÎì§
      formatDate,              // Í∏∞Ï°¥ Ìò∏ÌôòÏÑ± (Ï†àÎåÄ ÏãúÍ∞Ñ + ÏãúÍ∞ÑÎåÄ)
      formatRelativeDate,      // ÏÉÅÎåÄÏ†Å ÏãúÍ∞Ñ ("3ÏãúÍ∞Ñ Ï†Ñ")
      formatSmartDate: formatSmartDateInternal, // Ïª®ÌÖçÏä§Ìä∏Î≥Ñ Ïä§ÎßàÌä∏ Ìè¨Îß∑ÌåÖ
      formatPostDate,          // Í≤åÏãúÎ¨ºÏö© ÏµúÏ†ÅÌôî
      formatCommentDate,       // ÎåìÍ∏ÄÏö© ÏµúÏ†ÅÌôî
      
      // Í∏∞Ï°¥ Í∏∞Îä•
      getLocalizedText
    };
} 